
import React, { Component } from "react";
import { NavBar, Icon, Grid, InputItem, List, Stepper, Badge, Radio, Button, WhiteSpace } from "antd-mobile";
import { getFacebookUser } from './../appData';

const RadioItem = Radio.RadioItem;

const shippingTypes = [
    { value: '0', text: '직접수령' },
    { value: 'express', text: '택배' },
]

function calculateKo(qtyL, qtyS) {
    const c = Math.min(qtyL, qtyS)
    return c * 17000 + (qtyL - c) * 12000 + (qtyS - c) * 8000
}

function calculateCn(qtyL, qtyS) {
    const c = Math.min(qtyL, qtyS)
    return c * 100 + (qtyL - c) * 70 + (qtyS - c) * 50
}

class Form extends Component {
    state = {
        user: {},
        shippingType: '0',
        name: '',
        mobile: '',
        qtyL: 0,
        qtyS: 0,
        priceKo: 0,
        priceCn: 0,
        address: '',
    }
    componentDidMount = async _ => {
        // const user = getFacebookUser()
        // if (!user) {
        //     this.props.history.push('/login')
        //     return
        // }

        // this.setState({ user: user, name: user.name })
    }
    render = _ => <div>
        <NavBar
            mode="light"
            icon={<Icon type="left" />}
            onLeftClick={_ => { this.props.history.push('/') }}
        >구매하기</NavBar>
        <List renderHeader={() => '가격'}>
            <Grid
                columnNum={3}
                hasLine={false}
                square={false}
                data={[
                    { title: '큰것', size: '17cx25cm', priceKo: '₩12,000', priceCn: '¥70' },
                    { title: '작은것', size: '17x14cm', priceKo: '₩8,000', priceCn: '¥50' },
                    { title: '큰것+작은것', priceKo: <span><Badge text="HOT" /><br /><s>₩20,000</s><br /><font size='3'>₩17,000</font></span>, priceCn: <span><s>¥120</s> <font size='3'>¥100</font></span> },
                ]}
                renderItem={item => (
                    <div>
                        <p><font size="4">{item.title}</font></p>
                        <p>{item.size}</p>
                        <p>{item.priceKo}</p>

                    </div>
                )}
            />
            <font size="4">
            <p>외환하나은행</p>
            <p>029 13 27693 3</p>
            <p>유호선</p>
            </font>
            <WhiteSpace />
        </List>
        <List renderHeader={() => '구매 정보'}>
            <InputItem
                value={this.state.name}
                placeholder="input your name"
                onChange={name => this.setState({ name })
            }>
                이름
            </InputItem>
            <InputItem
                type="phone"
                placeholder="input your phone"
                value={this.state.mobile}
                onChange={mobile => this.setState({ mobile })}
            >전화번호</InputItem>
            <List.Item
                wrap
                extra={
                    <Stepper
                        style={{ width: '100%', minWidth: '100px' }}
                        showNumber
                        max={100}
                        min={0}
                        value={this.state.qtyL}
                        onChange={v => this.setState({
                            qtyL: v,
                            priceKo: calculateKo(v, this.state.qtyS),
                            priceCn: calculateCn(v, this.state.qtyS),
                        })}
                    />}
            >
                큰것
            </List.Item>
            <List.Item
                wrap
                extra={
                    <Stepper
                        style={{ width: '100%', minWidth: '100px' }}
                        showNumber
                        max={100}
                        min={0}
                        value={this.state.qtyS}
                        onChange={v => this.setState({
                            qtyS: v,
                            priceKo: calculateKo(this.state.qtyL, v),
                            priceCn: calculateCn(this.state.qtyL, v),
                        })}
                    />}
            >
                작은것
            </List.Item>
            <List.Item extra={this.state.priceKo}>가격</List.Item>
        </List>
        <List renderHeader={() => '수령방식'}>
            {shippingTypes.map(i => (
                <RadioItem key={i.value} checked={this.state.shippingType === i.value} onChange={() => this.setState({ shippingType: i.value })}>
                    {i.text}
                </RadioItem>
            ))}
            {this.state.shippingType !== 'express' ? '' :
                <InputItem
                    value={this.state.address}
                    placeholder="input your address"
                    onChange={address => this.setState({ address })}
                >
                    주소
            </InputItem>
            }
        </List>
        <WhiteSpace />
        <WhiteSpace />
        <Button type="warning" onClick={_ => console.log(this.state)}>구매</Button>
    </div>
}
export default Form


